.header{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 20px;
    position: sticky;
    top: 0;
    z-index: 100;
    background-color: white;
}

.header-logo{
    height: 23px;
    object-fit: contain;
    padding-left: 5px;
}

.header-left{
    display: flex;
    align-items: center;
}

.header-mid{
    display: flex;
    align-items: center;
    width: 40%;
    border: 1px solid black;
}

.searchTab{
    padding: 6.5px;
}

.header-mid>input{
    flex: 1;
    border: none;
}

.header_inputButton{
    width: 50px;
    border-left: 1px solid lightgray;
    background-color: #fafafa;
    color: gray; 
    padding: 0 0.9rem;
}
.header_inputButton:hover{
    color: red;
    font-weight: bold;
}

.header-right{
    display: flex;
    align-items: center;
}

.header_icon{
    margin-right: 8px;
}